(function() {
  'use strict';

  angular
    .module('blocks.edit-field', [])
    .directive('editField', editField)

  editField.$inject = [
    'toastr',
    '$timeout'
  ];

  function editField(toastr, $timeout) {
    return {
      restrict: 'E',
      replace: true,
      transclude: true,
      scope: {
        placeholder: '@',
        required: '@',
        mask: '@'
      },
      require: 'ngModel',
      templateUrl: 'html/blocks/edit-field/edit-field.html',
      link: function(scope, element, attrs, ngModel) {
        scope.editMode = false;

        scope.editModeOn = () => {
          let input;
          let query;

          if (scope.mask) {
            query = element[0].getElementsByClassName('edit-field-input-mask');
          } else {
            query = element[0].getElementsByClassName('edit-field-input');
          }

          $timeout(() => {
            input = angular.element(query);
            input[0].focus();
          }, 100);

          scope.fieldData = ngModel.$viewValue;
          scope.editMode  = true;
        }

        scope.editModeOff = () => {
          scope.fieldData = ngModel.$viewValue;
          scope.editMode  = false;
        }

        scope.editSave = () => {
          if (!(scope.fieldData) && (scope.required)) {
            toastr.error('    !');
          } else {
            ngModel.$setViewValue(scope.fieldData);
            ngModel.$render();
            scope.editModeOff();
          }
        }

        let queryResult  = element[0].getElementsByClassName('field-data-value');
        let spanElements = angular.element(queryResult);

        spanElements.bind('click', () => {
          scope.$apply(function() {
            scope.editModeOn();
          });

          event.preventDefault();
        });

        element.bind('keydown keypress', (event) => {
          if (event.which === 13) {
            scope.$apply(function() {
              scope.editSave();
            });

            event.preventDefault();
          }
        });

        ngModel.$render = function() {
          scope.fieldData = ngModel.$viewValue;
        };

        ngModel.$render();
      }
    }
  }

})();